import React, { Component } from 'react';
import { AnchorLink } from "gatsby-plugin-anchor-links";

class primarynav extends Component {
  render() {
	const navs = [
			{
				label: 'Business Units',
				link: 'business-units'
			},
			{
				label: 'Awards',
				link: 'awards'
			},
			{
				label: 'Directors',
				link: 'directors'
			},
			{
				label: 'Contact us',
				link: 'contact-us'
			}
		];
    return (
	<React.Fragment>
		<ul className="flex flex-row justify-end space-x-10 text-sm tracking-wide ">
			{navs.map((value, index) => {
				return<li key={index}>
					<AnchorLink
						to={"/#" + value.link}
						title={value.label}
						className="stripped"
						stripHash
					/>
					</li>
			})}
		</ul>
	</React.Fragment>
    )
  }
}

export default primarynav
